'use client'
import { useState, useEffect } from "react";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Slider } from "@/components/ui/slider";
import { Label } from "@/components/ui/label";
import { AlertCircle, Search, SlidersHorizontal } from "lucide-react";

export default function RestaurantMenu() {
  const [activeCategory, setActiveCategory] = useState(0); // 0 means all categories
  const [searchQuery, setSearchQuery] = useState("");
  const [vegFilter, setVegFilter] = useState("all"); // "all", "veg", "nonveg"
  const [maxSpiceLevel, setMaxSpiceLevel] = useState(5);
  const [filteredDishes, setFilteredDishes] = useState([]);
  const [showFilters, setShowFilters] = useState(false);
  
  // Initialize filtered dishes with all dishes
  useEffect(() => {
    setFilteredDishes(menuData.menu.dishes);
  }, []);

  // Restaurant menu data
  const menuData = {
    "menu": {
      "categories": [
        {
          "id": 1,
          "name": "Starters",
          "description": "Delicious appetizers to begin your meal",
          "type": "food"
        },
        {
          "id": 2,
          "name": "Main Course",
          "description": "Hearty main dishes",
          "type": "food"
        },
        {
          "id": 3,
          "name": "Beverages",
          "description": "Refreshing drinks",
          "type": "beverage"
        },
        {
          "id": 4,
          "name": "Desserts",
          "description": "Sweet endings to your meal",
          "type": "dessert"
        }
      ],
      "dishes": [
        {
          "id": 101,
          "name": "Paneer Tikka",
          "description": "Cottage cheese cubes marinated in spices and grilled in tandoor",
          "category_id": 1,
          "price": 250,
          "veg": true,
          "spice_level": 2,
          "tags": ["popular", "chef's special"],
          "preparation_time": 15,
          "ingredients": ["paneer", "yogurt", "spices", "bell peppers"]
        },
        {
          "id": 102,
          "name": "Butter Chicken",
          "description": "Tender chicken in a creamy tomato-based curry",
          "category_id": 2,
          "price": 350,
          "veg": false,
          "spice_level": 3,
          "tags": ["popular", "hot"],
          "preparation_time": 20,
          "ingredients": ["chicken", "butter", "tomato", "cream", "spices"]
        },
        {
          "id": 103,
          "name": "Mango Lassi",
          "description": "Refreshing yogurt-based drink with mango",
          "category_id": 3,
          "price": 120,
          "veg": true,
          "spice_level": 0,
          "tags": ["summer special"],
          "preparation_time": 5,
          "ingredients": ["yogurt", "mango pulp", "sugar"]
        },
        {
          "id": 104,
          "name": "Gulab Jamun",
          "description": "Soft fried dough balls soaked in sweet syrup",
          "category_id": 4,
          "price": 150,
          "veg": true,
          "spice_level": 0,
          "tags": ["traditional"],
          "preparation_time": 10,
          "ingredients": ["khoya", "flour", "sugar syrup"]
        }
      ],
      "tags": {
        "popular": "Frequently ordered dishes",
        "hot": "Trending right now",
        "chef's special": "Our signature creations",
        "summer special": "Seasonal offerings",
        "traditional": "Classic recipes"
      },
      "spice_scale": {
        "description": "1-5 scale where 1 is mild and 5 is very spicy",
        "levels": 5
      }
    }
  };

  // Helper function to filter dishes by category
  const getDishesByCategory = (categoryId) => {
    return menuData.menu.dishes.filter(dish => dish.category_id === categoryId);
  };

  // Helper function to get tag description
  const getTagDescription = (tagName) => {
    return menuData.menu.tags[tagName] || "";
  };
  
  // Apply all filters to dishes
  useEffect(() => {
    let result = menuData.menu.dishes;
    
    // Filter by active category if not viewing all categories
    if (activeCategory !== 0) {
      result = result.filter(dish => dish.category_id === activeCategory);
    }
    
    // Filter by search query
    if (searchQuery.trim() !== "") {
      const query = searchQuery.toLowerCase();
      result = result.filter(dish => 
        dish.name.toLowerCase().includes(query) || 
        dish.description.toLowerCase().includes(query) ||
        dish.ingredients.some(ing => ing.toLowerCase().includes(query))
      );
    }
    
    // Filter by veg/non-veg preference
    if (vegFilter === "veg") {
      result = result.filter(dish => dish.veg);
    } else if (vegFilter === "nonveg") {
      result = result.filter(dish => !dish.veg);
    }
    
    // Filter by spice level
    result = result.filter(dish => dish.spice_level <= maxSpiceLevel);
    
    setFilteredDishes(result);
  }, [activeCategory, searchQuery, vegFilter, maxSpiceLevel]);

  // Helper function to render spice level indicators
  const renderSpiceLevel = (level) => {
    const dots = [];
    for (let i = 0; i < 5; i++) {
      dots.push(
        <span key={i} className={`inline-block w-2 h-2 rounded-full mx-px ${i < level ? 'bg-red-500' : 'bg-gray-200'}`}></span>
      );
    }
    return <div className="flex items-center">{dots}</div>;
  };

  // Reset all filters to default values
  const resetFilters = () => {
    setActiveCategory(0);
    setVegFilter("all");
    setMaxSpiceLevel(5);
    setSearchQuery("");
  };

  return (
    <div className="max-w-4xl mx-auto p-4 bg-white rounded-lg shadow">
      <div className="text-center mb-8">
        <h1 className="text-3xl font-bold text-gray-800">Our Menu</h1>
        <p className="text-gray-600 mt-2">Discover our delicious offerings</p>
      </div>

      {/* Search and Filter Section */}
      <div className="mb-6">
        <div className="flex items-center gap-2 mb-4">
          <div className="relative flex-1">
            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
            <Input
              type="text"
              placeholder="Search dishes, ingredients..."
              className="pl-8"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          <button
            onClick={() => setShowFilters(!showFilters)}
            className="flex items-center gap-1 p-2 rounded-md border border-gray-200 hover:bg-gray-50"
          >
            <SlidersHorizontal className="h-4 w-4" />
            <span className="text-sm">Filters</span>
          </button>
        </div>

        {showFilters && (
          <div className="p-4 bg-gray-50 rounded-lg mb-4">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <Label htmlFor="diet-preference" className="text-sm font-medium">Diet Preference</Label>
                <Select
                  value={vegFilter}
                  onValueChange={setVegFilter}
                >
                  <SelectTrigger id="diet-preference">
                    <SelectValue placeholder="All Dishes" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Dishes</SelectItem>
                    <SelectItem value="veg">Vegetarian Only</SelectItem>
                    <SelectItem value="nonveg">Non-Vegetarian Only</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div>
                <Label className="text-sm font-medium">Max Spice Level: {maxSpiceLevel}</Label>
                <div className="pt-2">
                  <Slider
                    value={[maxSpiceLevel]}
                    max={5}
                    min={0}
                    step={1}
                    onValueChange={(value) => setMaxSpiceLevel(value[0])}
                  />
                  <div className="flex justify-between mt-1 text-xs text-gray-500">
                    <span>No Spice</span>
                    <span>Max Spice</span>
                  </div>
                </div>
              </div>

              <div>
                <Label htmlFor="category-select" className="text-sm font-medium">Category</Label>
                <Select
                  value={activeCategory.toString()}
                  onValueChange={(value) => setActiveCategory(parseInt(value))}
                >
                  <SelectTrigger id="category-select">
                    <SelectValue placeholder="All Categories" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="0">All Categories</SelectItem>
                    {menuData.menu.categories.map((category) => (
                      <SelectItem key={category.id} value={category.id.toString()}>
                        {category.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
            
            <div className="mt-4 flex justify-end">
              <button
                onClick={resetFilters}
                className="flex items-center gap-1 px-3 py-2 bg-gray-200 hover:bg-gray-300 rounded-md text-sm font-medium"
              >
                Reset Filters
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Results Section */}
      <div className="mb-6">
        <h2 className="text-xl font-semibold text-gray-800 mb-2">
          {activeCategory === 0 
            ? "All Menu Items" 
            : menuData.menu.categories.find(c => c.id === activeCategory)?.name}
        </h2>
        <p className="text-sm text-gray-600 mb-4">
          {filteredDishes.length} {filteredDishes.length === 1 ? 'dish' : 'dishes'} found
        </p>

        {filteredDishes.length === 0 ? (
          <div className="py-8 text-center text-gray-500">
            <p>No dishes match your search criteria.</p>
            <p className="text-sm mt-2">Try adjusting your filters or search query.</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {filteredDishes.map((dish) => (
              <Card key={dish.id} className="overflow-hidden">
                <CardHeader className="pb-2">
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="flex items-center">
                        {dish.name}
                        {dish.veg && (
                          <span className="ml-2 inline-block w-4 h-4 bg-green-500 rounded-sm" 
                                title="Vegetarian"></span>
                        )}
                      </CardTitle>
                      <CardDescription className="mt-1">{dish.description}</CardDescription>
                    </div>
                    <div className="text-lg font-bold">₹{dish.price}</div>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="flex flex-wrap gap-2 mb-3">
                    {dish.tags.map((tag) => (
                      <Badge key={tag} variant="outline" className="cursor-help" title={getTagDescription(tag)}>
                        {tag}
                      </Badge>
                    ))}
                  </div>
                  
                  {dish.spice_level > 0 && (
                    <div className="flex items-center mb-3">
                      <span className="text-sm text-gray-600 mr-2">Spice Level:</span>
                      {renderSpiceLevel(dish.spice_level)}
                    </div>
                  )}
                  
                  <div className="text-sm text-gray-600">
                    <div className="flex items-center gap-1">
                      <span className="font-medium">Preparation Time:</span> {dish.preparation_time} mins
                    </div>
                  </div>
                </CardContent>
                <Separator />
                <CardFooter className="pt-3 pb-3">
                  <div className="text-sm">
                    <span className="font-medium">Ingredients:</span>{" "}
                    {dish.ingredients.join(", ")}
                  </div>
                </CardFooter>
              </Card>
            ))}
          </div>
        )}
      </div>
      {/* <div className="mt-8 p-4 bg-gray-50 rounded-lg">
        <div className="flex items-center gap-2 mb-2">
          <AlertCircle size={18} className="text-amber-500" />
          <h3 className="text-lg font-medium">Spice Level Guide</h3>
        </div>
        <p className="text-sm text-gray-600 mb-2">{menuData.menu.spice_scale.description}</p>
        <div className="flex items-center gap-4">
          {[...Array(menuData.menu.spice_scale.levels)].map((_, i) => (
            <div key={i} className="flex items-center">
              <div className="flex">
                {[...Array(i + 1)].map((_, j) => (
                  <span key={j} className="inline-block w-2 h-2 rounded-full mx-px bg-red-500"></span>
                ))}
              </div>
              <span className="ml-2 text-sm">Level {i + 1}</span>
            </div>
          ))}
        </div>
      </div> */}

      <div className="mt-6 text-sm text-gray-500 text-center">
        <p>Please inform your server of any allergies or dietary restrictions.</p>
      </div>
    </div>
  );
}